.Appointments {
    .header {
        display: flex;
        justify-content: space-between;
        padding: 8px 0;
        border-bottom: 1px solid #C8CED0;
        margin-top: 24px;
        margin-bottom: 24px;
        p{
            color: #171A1C;
            font-size: 19px;
            font-style: normal;
            font-weight: 500;
            line-height: 19px;
        }
        button{
            padding: 0;
        }
        margin-bottom: 24px;
    }

    tbody {
        tr {
            &:nth-child(even) {
                background: #c8ced071;
            }

            &:nth-child(odd) {
                background: #c8ced022;
            }
        }
        td{
            &.mode,&.status{
                &::first-letter{
                    text-transform: uppercase;
                }
            }

        }
    }
}

@media screen and (max-width:992px) {
    .weektabs-modal .tabs-container .time-sections{
        max-height: 50vh;
        max-width: 80%;
        overflow-y: auto;
        flex-wrap: wrap;
    }
    .Appointments{
    }
}

